body {
    background-color: #ffffff;
    margin: 0;
    font-family: Helvetica, sans-serif;
}

.App {
    text-align: center;
    max-width: 100%;
    margin: 0 auto;
}

.App header {
    background-color: rgb(255, 255, 255);
    border-bottom: 0.1px solid #dcdcdc;
    height: 50px;
    color: white;
    position: fixed;
    width: 100%;
    max-width: 100%;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 99;
    padding: 10px;
    box-sizing: border-box;
    box-shadow:0px -15px 30px #000000;
}
.App section {
    justify-content: center;
    background-color: #ffffff
}
.App section,
main {
    display: flex;
    flex-direction: column
}
main {
    padding: 10px;
    height: 100%;
    margin-top: 8vh;
    margin-bottom: 51px;
    overflow-y: scroll; 
}
main::-webkit-scrollbar {
    width: 0.15rem;
}

main::-webkit-scrollbar-track {
    background: #ffffff;
}

main::-webkit-scrollbar-thumb {
    background: #aaaaaa;
}

form {
    height: 45px;
    position: fixed;
    bottom: 0;
    max-width: 100vw;
    min-width: 100vw;
    display: flex; 
    font-size: 1.5rem;
    margin-bottom: 0.8%;
    border-radius: 25px;
    display: flex;
    justify-content: center;
}
.message_box{
    display: flex;
    border-radius: 25px;
    box-shadow: 0px 5px 20px #6b6b6b64;
}
@media only screen and (max-width: 600px) {
    form {
        margin-bottom: 4%;
    }
}
input {
    display: flex;
    flex-direction: row;
    width: 65vw;
    font-size: 1rem;
    background: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    height: 45px;
    outline: none;
    border-radius: 25px;
    border:1px solid #dcdcdc;
    border-right: none;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    padding: 0px 20px;

}

button {
    background-color: rgb(255, 255, 255);
    border: none;
    height: auto;
    color: #007AFF;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
    font-size: 1.25rem;
}

.sign-out button {
    margin: 0;
    padding: 0 ;
    background-color: rgb(255, 255, 255);
    border: none;
    height: min-content;
    color: #007AFF;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
    font-size: 1.25rem;
}

button:disabled {
    color: #000000c9;
    cursor: not-allowed;
}


.sign-in {
    color:  #ffffff;
    background: #007AFF;
    font-weight: bolder;
    max-width: 400px;
    margin: 0 auto;
    padding:10px;
    border-radius: 20px;
    box-shadow: 0px 5px 10px #000000b3;
    position: fixed;
    bottom: 0;
    margin-bottom: 20px;
}
.sign-in:hover{
    
    border: none;
    color:  #007AFF;
    background: #ffffff;
    box-shadow: 0px 5px 5px #525252b3;

}


.sendbtn {
    padding-right: 20px;
    background: rgb(255, 255, 255);
    border: none;
    color: #007AFF;
    font-weight:bold;
    font-size: 1rem;
    height: 47px;
    width: min-content;
    max-width: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border:1px solid #dcdcdc;
    border-left: none;
    border-radius: 0;
    border-bottom-right-radius: 25px;
    border-top-right-radius: 25px;
}

ul,
li {
    text-align: left;
    list-style: none;
}

p {
    display: flex;
    flex-direction: column;
    word-wrap: break-word;
    overflow-wrap: break-word;
    max-width: 60%;
    min-width: none;
    height: max-content;
    margin: 4px;
    line-height: 15px;
    position: relative;
    color: white;
}

.message {
    display: flex;
    align-items: center;
    line-height: 1.4;
}


.sent {
    flex-direction: row-reverse;
}

.sent p {
    color: rgb(0, 0, 0);
    background-color:#dcdcdc;
    border-radius: 20px;
    align-self: flex-end;
    text-align: start;
    padding: 15px 15px;

}
.received p {
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    border:0.1px solid #dcdcdc;
    border-radius: 20px;
    align-self: flex-end;
    text-align: start;
    padding:15px;
    padding-top: 5px;
    padding-bottom: 10px;
    
    word-wrap: break-word;
    overflow-wrap: break-word;
}
img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: auto 0 5px;
}
.sent img{
    visibility: hidden;
    display: none;
}
.usernamesent{
    display: none;
    visibility: hidden;
} 
.username {
    text-transform: capitalize;
    line-height: 0;
}
.messageText span{
    align-self: flex-start;
    float: right;
}